/* navbar.css */

.active {
  background-color: #1d4ed8;
}
nav {
  background-color: #333; /* Change the background color as needed */
  color: #fff; /* Change the text color as needed */
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 10000;
}

nav .nav__logo {
  font-size: 1.5rem; /* Adjust the logo font size */
  text-decoration: none;
  color: #fff;
  margin: 1rem;
  font-weight: bold; /* Change the color of the logo text */
}

nav ul {
  display: flex;
}

nav ul li {
  list-style: none;
}

nav ul li a {
  display: block;
  text-decoration: none;
  color: #fff;
  padding: 0.5rem;
  margin: 0 0.5rem;
  border-radius: 0.5rem;
}

nav ul li a:not(.active):hover {
  background-color: #172554;
}

nav .menu{
  display: none;
  position: absolute;
  top: 1.45rem;
  right: 0.5rem;
  flex-direction: column;
  justify-content: space-between;
  width: 2.25rem;
  height: 2rem;
}

nav .menu span{
  height: 0.4rem;
  width: 100%;
  background-color: #fff;
  border-radius: 0.2rem;
 

}

@media (max-width:600px) {
  nav .menu{
    display: flex;
  }

  nav{
    flex-direction: column;
    align-items: center;
  }
  nav ul{
    display: none;
    flex-direction: column;
    width: 100%;
    margin-bottom: 0.75rem;
  }
  nav ul.open{
    display: flex;
  }

  nav ul li{
    width: 100%;
    text-align: center;

  }

  nav ul li a {
    margin: 0.2rem 0.5rem;
  }
}


